<!--  -->
<template>
  <div>
        <Zi bool1="true"  goback="true"  con="账户信息">
        <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
      </Zi>
      <div class="main">
          <ul>
              <li>
                <p>头像</p>
                <input id="result" ref="upload"   @change="upload" type="file" style="opacity: 0" >
                 <img v-if="this.$store.state.obj.login==false" data-v-0fc4ab7a="" src="//elm.cangdu.org/img/default.jpg" class="privateImage">
         
                  <img v-else :src="'//elm.cangdu.org/img/'+headPhoto" class="headportrait-div-top">
                  <!-- </p> -->
              </li>
              <router-link to="/setusername" tag="li">
                   <p>用户名</p>
                  <p  class="right">{{ this.$store.state.obj.username}}>
                  </p>
              </router-link>
              <router-link to="/address" tag="li">
                    <p>收货地址</p>
                  <p  class="right">>
                  </p>
             </router-link>
              <li class="bang">
                  账号绑定
              </li>
              <li>
                  <p>
                      <img data-v-c2d94162="" src="data:image/png;base64," alt="" style="display: inline-block; margin-right: 0.4rem;">
                      手机
                  </p>
                  <p class="right">>  </p>
              </li>
              <li  class="bang">
                  安全设置
              </li>
              <li>
                  <p>登陆密码</p>
                  <router-link to="/forget" tag="p"  class="right">修改>
                  </router-link >
              </li>
          </ul>
               
              <button class="tui" @click="tui">退出登录</button>
          <div class="tishi" v-show="bool2==true">
        <div class="tishi2">
          <span class="tishi_s1"></span><span class="tishi_s2"></span>
        
        </div>
         <p class="tishi_p"> {{val}}</p>
         <h1>是否确认推出</h1>
        <p>
           <button class="que" @click="loading">再等等</button>
            <button class="que" @click="que">确认</button>
        </p>
      </div>
      </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
import Zi from "../../components/header"
import Foot from "../../components/foot"
export default {
  data () {
    return {
        user_id:"",
        headPhoto:"",
        bool2:false,
    };
  },
beforeMount(){
  // 获取id
  this.headPhoto=this.$store.state.img
  this.user_id=this.$store.state.obj.user_id
},
  components: {Zi,Foot},

  computed: {},

  mounted(){
       

  
  },

  created(){},

  methods: {

// 换头像

upload(){
  
  

  // var input =this.$refs.upload
  // 给input一个ref属性 获取真是DOM 我给的名字是uplode
  // let data=new FormData()
  // 创建一个FormData对象  这个对象主要用于发送表单数据
  // data.append("file",input.files[0])
  // 给他追加一个文件 因为input type=file 能获取文件 所以上面给的是<input type="file"/>
  // input.files[0]能获取从本地选的文件是什么
  // this.http.post("https://elm.cangdu.org/eus/v1/users/"+this.user_id+"/avatar",{headers:{"Context-type":"multipart/form-data"}}).then((d)=>{
  //   console.log(d)
  // })
  
  // 因为原来发送到是字符串 所以请求头都是application/json
  // 但是这次是发送图片也就是文件 所以要修改请求头 header:{"Context-type":"multipart/form-data"}}
   const userUploadAtt= (File,config) =>this.http.post('https://elm.cangdu.org/eus/v1/users/'+this.user_id+'/avatar',File,config)
   let input =this.$refs.upload
   //new 一个对象
   let data  = new FormData()
   //用FormData.append为表单添加建值对到表单中
   data.append('file',input.files[0])
   userUploadAtt(data,{headers:{'Content-Type': 'multipart/form-data'}}).then(res =>{
      console.log(res)
      this.headPhoto = res.data.image_path;
      this.$store.commit("gaiImg",this.headPhoto)
})
},

 *

      tui(){
        this.bool2=true
      },
      loading(){
          this.bool2=false
      },
      que(){
          this.bool2=false
          this.$store.commit("tui")
          this.$router.push("/my")
      }
      

    
  
}
}

</script>
<style scoped lang="scss">

.bang{
background: #f5f5f5!important;
margin-left: 0.2rem;
}
.main{
  .tui{
    display: block;
    margin: 0.2rem auto;
    width: 90%;
    line-height: 0.7rem;
    border-radius: 5px;
    text-align: center;
    background: #d8584a;
    font-size: .3rem;
    color: #fff;
  }
    margin-top: 1.2rem;
    ul{
        
        li{
          img{
              float: left;

           margin-top: 0.2rem;
          width: 0.6rem;
          height: 0.6rem;

          border-radius: 50%;
          display: block;
          }
          display: flex;
            background: white;
            overflow: hidden;
            width: 100%;
          line-height: 0.8rem;border-bottom:1px solid #ccc;
          input{
            display: block;
            width: 70%;
            height: 100%;
            border: none;
            line-height: 0.8rem;
            height: 0.8rem;
          }
        
          .right{
            width: 80%;
            float: right;
            text-align: right;
              margin-right: 0.2rem;
              float: right;line-height:1rem;
              
       img{
           display: block;
           float: left;

           margin-top: 0.2rem;
          width: 0.6rem;

          border-radius: 50%;
        }
          }
            p{display: block;line-height: 1rem;float: left;margin-left: 0.2rem;
            width: 20%;
           input,img{
                margin-right: -0.05rem!important;
            }
          
           
      
            }
        }
    }
}
  .tishi{
    p{
      text-align: center;
    }
    h1{
      text-align: center;
    }
  .tishi_p{
  text-align: center;
 margin-top: 0.4rem;
  font-size: 0.3rem;
}
  .que{
    width: 40%;
  }
  border-radius: 3%;
  position: fixed;
left: 10%;
top: 12%;
  width: 80%;
  height: 40%;
 border:1px solid #ffffff;
 background: white;
 z-index: inherit;
 .tishi2{
       width: 1.5rem;
    height: 1.5rem;
    border: .05rem solid #f8cb86;
    border-radius: 50%;
    margin: 0 auto;
    margin-top: 0.35rem;
    .tishi_s1{
          width: .05rem;
    height: 0.8rem;
    background-color: #f8cb86;
    margin-left: 0.7rem;
    margin-top: 0.2rem;
    display:block
}

.tishi_s2{
      width: .15rem;
    height: .15rem;
    border: 1px;
    display:block;
    border-radius: 50%;
    margin-top: .2rem;
    background-color: #f8cb86;
     margin-left: 0.65rem;
    margin-top: 0.2rem;
}

 }
}

</style>


